@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
body {
  margin: 0;
  padding: 0;
  background-color: rgb(133, 141, 110) !important;
}

.navbar {
  background-color: bisque !important;
}

li a {
  color: rgb(3, 109, 8) !important;
  font-family: "Gilda Display", serif;
  font-weight: 1000;
  font-size: 20px;
}

.transpLogo {
  width: 100%;
  height: auto;
  max-width: 450px;
}

.dropdown-menu {
  background-color: rgb(73, 100, 73);
}

.dropdown-item {
  color: bisque !important;
  text-shadow: 2px 2px 2px black;
}

.dropdown-item:hover {
  background-color: rgb(133, 141, 110);
}

@media screen and (max-width: 575px) {
  .navbar-brand {
    max-width: 300px;
  }
}

.slideText {
  font-family: "Corben", cursive;
  color: bisque;
}

.infoCard {
  padding: 20px;
  background-color: bisque !important;
  font-family: "Old Standard TT", serif;
  font-weight: 900;
  width: 700px;
  height: auto;
  margin: 10px;
  align-content: center;
  margin-top: 35px;
}

.hours {
  text-align: center;
}

.mapsFb {
  display: flex;
  justify-content: center;
  padding-top: 10px;
}

.FbContainer {
  width: 150px;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  margin-top: 20px;
}

@media screen and (max-width: 767px) {
  .infoCard {
    width: 400px;
  }
}

.laviathan {
  position: absolute;
  z-index: 1;
  margin-left: -135px;
  left: 50%;
  margin-top: 400px;
}

.hercules {
  display: flex;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 15px;
  max-width: 800px;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 160px;
  margin-top: auto;
  margin-bottom: 0;
}

.px-2 {
  color: bisque !important;
  text-shadow: 2px 2px 2px black;
}

.copyright {
  color: bisque !important;
}

.footerBorder {
  border-color: rgb(214, 183, 157) !important;
}

.headingsText {
  font-size: 2px;
  font-family: "Dancing Script";
  text-align: center;
  color: bisque;
  margin-top: 30px;
  margin-bottom: 25px;
  text-shadow: 2px 2px 2px black;
}

.profPic {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 500px;
  height: auto;
  margin-left: 10px;
  border: bisque;
  border-style: solid;
  border-width: 3px;
}

.bioCard {
  font-family: "Old Standard TT", serif;
  color: bisque !important;
  width: auto;
  height: 100%;
  /* max-width: 1500px;
  max-height: 400px; */
  background-color: rgb(74, 97, 78) !important;
  text-align: center;
}

.bioBio {
  display: flex;
  line-height: 1.3;
  font-size: 2vw;
  text-align: center;
  position: relative;

  text-shadow: 1px 1px 1px black;
}

.robot {
  display: flex;
  justify-content: center;
}

.bioName {
  font-size: 3vw;
  text-shadow: 1px 1px 1px black;
}

@media screen and (min-width: 1500px) {
  .profPic {
    width: 100%;
    max-width: 400px;
    height: auto;
    max-height: 600px;
    margin-left: auto;
    margin-right: auto;
  }

  .bioCard {
    padding: 20px;
    max-height: 600px;
    margin-right: 60px;
    max-width: 1285px;
  }

  .bioBio {
    font-size: 30px;
  }
}
