.staffPic {
  width: 100%;
  max-width: 800px;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  box-sizing: border-box;
  border: 3px bisque;
  border-style: solid;
}

.staffTextDiv {
  text-align: center;
  font-family: "Old Standard TT", serif;
  color:bisque;
  text-shadow: 2px 2px 2px black;
  margin-bottom: 20px;
  margin-left: 10px;
  margin-right: 10px;
}

@media screen and (max-width: 575px) {

  .staffText {
    font-size: 16px;
  }

  .staffPic {
    max-width: 375px;
  }
}




.devMessage {
  text-align: center;
  font-family: "BioRhyme", serif;
  font-weight: 900;
  font-size: 18px;
  color: bisque !important;
  text-shadow: 2px 2px 2px black;
  background-color: rgb(73, 100, 73) !important;
  padding: 5px;
  margin-top: 30px;
  margin-bottom: 50px;
}

.containerZ2 {
  text-align: center;
  font-family: "BioRhyme", serif;
  font-weight: 900;
  font-size: 18px;
  color: bisque !important;
  text-shadow: 2px 2px 2px black;
  background-color: rgb(73, 100, 73) !important;
  padding: 5px;
  margin-top: 30px;
  margin-bottom: 50px;
}

.centeredText {
  margin-left: auto;
  margin-right: auto;
  display: flex;
  max-width: 600px;
}

.updateText h1 {
  color: bisque;
  text-shadow: 2px 2px 2px black;
  text-align: center;
  font-size: 25px;
  margin-top: 7px;
}

.updateContainer {
  height: auto;
  width: 100%;
  max-width: 500px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

hr {
  border: 0;
  height: 4px;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0)
  );
}

@media screen and (min-width: 575px) {
  .devMessage {
    width: 100%;
    height: auto;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
  }

  .containerZ2 {
    width: 100%;
    height: auto;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media only screen and (max-width: 575px) {
  .devMessage {
    font-size: 13px;
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
  }

  .containerZ2 {
    font-size: 14px;
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
  }

  .containerZ2 h1 {
    text-align: left;
    margin: 5px;
  }

  .containerZ2 h3 {
    margin: 3px;
    padding: 4px;
    text-align: right;
    font-size: 14px;
  }

  .updateContainer {
    margin-left: auto;
    margin-right: auto;
    max-width: 400px;
    padding: 10px;
  }

  .updateText h1 {
    font-size: 13px;
  }
}

.suiterultrasoundpic {
  width: 100%;
  height: auto;
  max-width: 700px;
  margin-top: 20px;
  border-style: solid;
  border-color: bisque;
}

.ultraDiv {
  border-radius: 5%;
  background-color: rgb(255, 228, 196, 0.5);
  z-index: 2;
  font-weight: 900;
  font-family: "BioRhyme", serif;
  margin-top: 15px;
  height: auto;
}

.ultraBtn {
  width: 400px;
  padding: 10px !important;
  background-color: rgb(73, 100, 73) !important;
  color: bisque !important;
  font-family: "Corben", cursive !important;
  font-weight: 400 !important;
  text-shadow: 2px 2px 2px black;
  word-spacing: 2px;
  letter-spacing: 1.4px !important;
}

.bioWithButton {
  text-align: center;
}

.ultraParentDiv {
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  color: rgb(121, 91, 41);
  text-shadow: 1px 1px 2px rgb(255, 255, 255);
}

